.startups-row-1 {
  display: flex;
  padding: 112px 64px;
  flex-direction: column;
  align-items: center;
  gap: 80px;
  background: #012a21;
}

.startups-row-1 div {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  align-self: stretch;
}

.startups-row-1 h1 {
  color: var(--Text-alternate, #fff);
  text-align: center;

  /* Heading/Desktop/H1 */
  font-family: Roboto;
  font-size: 56px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%; /* 67.2px */
  margin-top: 0;
  margin-bottom: 0;
}

.startups-row-1 p {
  color: var(--Text-alternate, #fff);
  text-align: center;

  /* Text/Medium/Normal */
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 27px */
}

.startups-row-1 button {
  display: flex;
  padding: 12px 24px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  background-color: black;
  color: white;
  border: none;
}

